import React, { Component } from 'react'

class Skills extends Component {
    constructor(props)
    {
        super(props);
        this.state={
            'frontEndSkills':['HTML','CSS','Javascript','React js'],
            'backEndSkills':['Node js', 'PostgreSQL']
        };
    }

    render() {
        return (
            <div className="container">
                <section className="" data-section="skills">
                    <div className="skills">
                        <h1 className="subtopic">FrontEnd Skills</h1>
                        <ul>
                        {this.state.frontEndSkills.map((value)=>{
                            return <li>{value}</li>
                        })}
                        </ul>
                        <h1 className="subtopic">BackEnd Skills</h1>
                        <ul>
                        {this.state.backEndSkills.map((value)=>{
                            return <li>{value}</li>
                        })}
                        </ul>
                    </div>
                </section>
            </div>
            
            )
        }
    }
    
    export default Skills
    