import React, { Component } from 'react'
import profilepic from '../img/about.jpg';


class Sidebar extends Component {
  render() {
    return (
      <div>
        <div>
          <aside className="border js-fullheight">
            <div className="text-center">
              <div className="" >
                <img src={profilepic} className="profilepic"></img>
              </div>
              <h1 id="colorlib-logo"><a href="index.html">Vidhya Chaudhary</a></h1>
              <span className="email"><i className="icon-mail"></i> vidhyachaudhary08@gmail.com</span>
              <div class="social">
            <a href="https://github.com/vidhya08" target="_blank"><i class="fab fa-github"></i></a>
            <a href="https://Linkedin.com/in/vidhya-chaudhary" target="_blank"><i class="fab fa-linkedin-in"></i></a>
            </div>
            </div>
          </aside>
        </div>
      </div>
    )
  }
}
export default Sidebar